<template>
    <div class="main-content-home-cours">

        <div class="middle-circle" ref="parallax4"></div>

        <div class="left-container-home-cours" ref="parallax">
            <div class="search-container">
                <input class="search__input" type="text" placeholder="Search">
                <span class="material-icons">search</span>
            </div>
            <a class="btn-slice" @click="nav()">
                <div class="top"><span>Afficher tout</span></div>
                <div class="bottom"><span>Afficher tout</span></div>
            </a>
        </div>

        <span class="cours-home-separator" ref="parallax3"></span>

        <div class="right-container-home-cours" ref="parallax2">
            <div class="shpi-button-list-container">
                <div class="home-shpi-container">
                    <div class="button-shpi-container" id="shpi1a">
                        <span>SHPI 1A</span>
                    </div>
                </div>
                <div class="home-shpi-container">
                    <div class="button-shpi-container" id="shpi2a">
                        <span>SHPI 2A</span>
                    </div>
                </div>
                <div class="home-shpi-container">
                    <div class="button-shpi-container" id="shpi3a">
                        <span>Ingénieur 3A</span>
                    </div>
                </div>
            </div>
            <div class="shpi-text-container" id="shpitext0">
                <span class="module">
                    {{ curentCours[0].module[0] }}
                </span>
                <div class="matter-container">
                    <span id="test" class="matter" v-for="(matter, index) in curentCours[1].matter" :key="matter" @click="test(index)"> {{ matter }} </span>
                </div>
            </div>
            <div class="shpi-text-container" id="shpitext1">
                <span class="module">
                    {{ curentCours[0].module[1] }}
                </span>
                <div class="matter-container">
                    <span class="matter" v-for="(matter, index) in curentCours[2].matter" :key="matter" @click="test(curentCours[1].matter.length + index)"> {{ matter }}</span>
                </div>
            </div>
            <div class="shpi-text-container" id="shpitext2">
                <span class="module">
                    {{ curentCours[0].module[2] }}
                </span>
                <div class="matter-container">
                    <span class="matter" v-for="(matter) in curentCours[3].matter" :key="matter" @click="test(curentCours[2].matter.length + index)"> {{ matter }}</span>
                </div>
            </div>
            <div class="shpi-text-container" id="shpitext3">
                <span class="module">
                    {{ curentCours[0].module[3] }}
                </span>
                <div class="matter-container">
                    <span class="matter" v-for="(matter) in curentCours[4].matter" :key="matter"  @click="test(curentCours[3].matter.length + index)"> {{ matter }}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { gsap } from 'gsap';


export default {
    data() {

        const shpi1aCours = [
            { module: ['Mathématique', 'Physique', 'Physique appliquée', 'Outils'] },
            { matter: ['Algèbre', 'Analyse'] },
            { matter: ['Electrocinétique', 'Electrostatique', 'Magnétostatique', 'Mécanique', 'Optique Géométrique'] },
            { matter: ['Automatique', 'Chimie des Matériaux', 'Thermodynamique'] },
            { matter: ['Algorithmique', 'Outil Mathématiques', 'Probabilités'] },
        ]

        const shpi2aCours = [
            { module: ['Mathématique', 'Physique', 'Physique appliquée', 'Informatique'] },
            { matter: ['Algèbre', 'Analyse', 'Analyse Numérique'] },
            { matter: ['Electromagnétisme', 'Physique des Ondes', 'Optique Ondulatoire'] },
            { matter: ['Automatique', 'Calcul Formel', 'Thermodynamique'] },
            { matter: ['Algorithmique', 'Architecture et Systèmes d\'exploitation', 'Bases de Données Relationnelles', 'Génie Logiciel', 'Programmation Orienté Objets'] }
        ]


        return {
            isShpi1: false,
            isShpi2: false,
            isShpi3: false,
            shpi1aCours,
            shpi2aCours,
            curentCours: shpi2aCours,
            releve1: false,
            releve2: false
        }
    },
    methods: {
        handleScroll() {
            const scrollTop = window.pageYOffset - 400;
            const item1 = this.$refs.parallax;
            const item2 = this.$refs.parallax2;
            const item3 = this.$refs.parallax3;
            const item4 = this.$refs.parallax4;
            const halfway = window.innerWidth / 2.1;
            const halfhei = window.innerHeight / 4;


            if((scrollTop)*0.4 < halfhei) {
                gsap.to(item3, {
                    y: (scrollTop - 700)*1.4,
                    ease: 'none',
                    opacity: (scrollTop / halfhei) * 0.5
                })
            } else {
                gsap.to(item3, {
                    y: window.innerHeight / 100,
                    opacity: 1,
                    ease: 'none',
                });
            }

            if (scrollTop*1.4 < halfway) {

                gsap.to(item1, {
                    x: scrollTop * 1.4,
                    ease: 'none',
                    opacity: (scrollTop / halfway),
                });
                gsap.to(item2, {
                    x: -scrollTop * 1.4,
                    ease: 'none',
                    opacity: (scrollTop / halfway),
                });
                gsap.to(item4, {
                    opacity: (scrollTop / halfway),
                    ease: 'none',
                });
            } else {
                gsap.to(item4, {
                    opacity: 1,
                    ease: 'none',
                });
                gsap.to(item1, {
                    x: window.innerWidth / 2.1,
                    opacity: 1,
                    ease: 'none',
                });
                gsap.to(item2, {
                    x: -window.innerWidth / 2.03,
                    opacity: 1,
                    ease: 'none',
                });
            }
        },
        displayActive: function (target) {
            switch (target) {
                case 1:
                    this.isShpi1 = !this.isShpi1;
                    this.isShpi2 = false;
                    this.isShpi3 = false;
                    break;
                case 2:
                    this.isShpi2 = !this.isShpi2;
                    this.isShpi1 = false;
                    this.isShpi3 = false;
                    break;
                case 3:
                    this.isShpi3 = !this.isShpi3;
                    this.isShpi2 = false;
                    this.isShpi1 = false;
                    break;
                default:
                    break;
            }
        },

        displayBoxes: function (target) {
            const container = document.querySelectorAll('.shpi-text-container');
            const container0 = document.getElementById('shpitext0');
            const container1 = document.getElementById('shpitext1');
            const container2 = document.getElementById('shpitext2');
            const container3 = document.getElementById('shpitext3');
            if (target == 1) {
                if (this.releve1) {
                    for (let i = 0; i < container.length; i++) {
                        container[i].classList.add('notransition');
                        container[i].style.width = '25%';
                        container[i].style.height = '7%';
                        container[i].style.top = '20%';
                        container[i].style.visibility = 'hidden';
                        container[i].style.marginLeft = '5%';
                        container[i].offsetHeight;
                        container[i].classList.remove('notransition');
                        container[i].style.transition = 'height 1s, top 1s, position 1s, margin-left 1s 1s';
                    }
                }
                else {
                    for (let i = 0; i < container.length; i++) {
                        container[i].style.width = '21%'
                        container[i].style.height = '60%'
                        container[i].style.top = '30%'
                        container[i].style.visibility = 'visible'
                    }
                    container1.style.marginLeft = '28%';
                    container2.style.marginLeft = '51%';
                    container3.style.marginLeft = '74%';
                }
            }
            if (target == 2) {
                if (this.releve2) {
                    for (let i = 0; i < container.length; i++) {
                        container[i].classList.add('notransition');
                        container[i].style.width = '25%';
                        container[i].style.height = '7%';
                        container[i].style.top = '20%';
                        container[i].style.visibility = 'hidden';
                        container[i].style.marginLeft = '5%';
                        container[i].offsetHeight;
                        container[i].classList.remove('notransition');
                        container[i].style.transition = 'height 1s, top 1s, position 1s, margin-left 1s 1s';
                    }
                }
                else {

                    for (let i = 0; i < container.length; i++) {

                        container[i].classList.add('notransition');
                        container[i].style.marginLeft = '37%';
                        container[i].offsetHeight;
                        container[i].classList.remove('notransition');


                        container[i].style.transition = 'height 1s, top 1s, position 1s, margin-left 1s 1s';
                        container[i].style.width = '21%'
                        container[i].style.height = '60%'
                        container[i].style.top = '30%'
                        container[i].style.visibility = 'visible'
                    }
                        container0.style.marginLeft = '5%';
                        container1.style.marginLeft = '28%';
                        container2.style.marginLeft = '51%';
                        container3.style.marginLeft = '74%';
                    // }, 0);

                }
            }

        },
        nav: function () {
            this.$router.push({ path: '/cours', query: { research: '', year: '', matter: ''}})
        },

        test: function(i) {
            const matterSpan = document.querySelectorAll('.matter');
            let year = 0;
                if(this.releve1) year = 1;
                else year = 2;
            this.$router.push({ path: '/cours', query: { research: '', year: year, matter: matterSpan[i].innerHTML } })
        }
    },
    watch: {
        isShpi1: function (bool) {
            const element = document.getElementById(`shpi1a`);
            if (bool == false) {
                element.classList.remove('active')
                this.releve1 = false;
            } else {
                element.classList.toggle('active')
                this.releve1 = true;
                this.releve2 = false;
            }
        },
        isShpi2: function (bool) {
            const element = document.getElementById(`shpi2a`);
            if (bool == false) {
                element.classList.remove('active')
                this.releve2 = false;
            } else {
                element.classList.toggle('active')
                this.releve1 = false;
                this.releve2 = true;
            }
        },
        isShpi3: function (bool) {
            const element = document.getElementById(`shpi3a`);
            if (bool == false) {
                element.classList.remove('active')
            } else {
                if (this.releve1) this.displayBoxes(1);
                element.classList.toggle('active')
            }
        },
    },
    beforeUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    },
    unmounted() {
        window.removeEventListener('scroll', this.handleScroll);
    },
    mounted() {
        // const parallaxItemLeft = document.querySelector('.left-container-home-cours');
        // const parallaxItemRight = document.querySelector('.right-container-home-cours');



        window.addEventListener('scroll', this.handleScroll);


        // window.addEventListener('scroll', () => {
        //     const scrollPosition = window.scrollY;

        //     if (scrollPosition > 0) {
        //         parallaxItemLeft.classList.add('active');
        //         parallaxItemRight.classList.add('active');
        //     } else {
        //         parallaxItemLeft.classList.remove('active');
        //         parallaxItemRight.classList.remove('active');
        //     }
        // });

        const shpi1a = document.getElementById('shpi1a');
        shpi1a.addEventListener('click', () => {
            this.curentCours = this.shpi1aCours;
            if(this.releve2) this.displayBoxes(2);
            this.displayActive(1);
            this.displayBoxes(1);
        })
        const shpi2a = document.getElementById('shpi2a');
        shpi2a.addEventListener('click', () => {
            this.curentCours = this.shpi2aCours;
            if(this.releve1) this.displayBoxes(1);
            this.displayActive(2);
            this.displayBoxes(2);
        })
        const shpi3a = document.getElementById('shpi3a');
        shpi3a.addEventListener('click', () => {
            this.displayActive(3);
        })

    }
}
</script>

<style lang="scss">
:root {
    font-size: calc(16px + (24 - 16) * (100vw - 320px) / (1920 - 320));
}

@mixin transition($property: all, $duration: 0.45s, $ease: cubic-bezier(0.65, 0, .076, 1)) {
    transition: $property $duration $ease;
}

.main-content-home-cours {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    // background-image: url('../../../assets/2.svg');
    // background-size: cover;
    // background-repeat: no-repeat;
    position: relative;
    overflow-y: visible;
    overflow-x: clip;
    

    .middle-circle {
        background-color: #fff;
        height: 10vw;
        width: 10vw;
        position: absolute;
        z-index: 0;
        top: 21%;
        left: 23%;
        clip-path: circle(50% at 50% 50%);
        opacity: 0;
    }

    .left-container-home-cours {
        width: calc(50% - 1px);
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: -50%;
        opacity: 0;
        // transition: left 1s ease-in-out;
        // transition-delay: 0.5s;

        .search-container {
            display: flex;
            align-items: center;
            width: 80%;
            margin-bottom: 20%;

            .search__input {
                width: 100%;
                padding: 12px 24px;

                background-color: transparent;
                transition: transform 250ms ease-in-out;
                font-size: 14px;
                line-height: 18px;

                color: #575756;
                background-color: transparent;
                border-radius: 50px;
                border: 1px solid #575756;
                transition: all 250ms ease-in-out;
                backface-visibility: hidden;
                transform-style: preserve-3d;

                &::placeholder {
                    color: color(red a(0.8));
                    text-transform: uppercase;
                    letter-spacing: 1.5px;
                }

                &:hover,
                &:focus {
                    padding: 12px 0;
                    outline: 0;
                    border: 1px solid transparent;
                    border-bottom: 1px solid red;
                    border-radius: 0;
                    background-position: 100% center;
                }

            }

            .material-icons {
                padding: 0.5em;
                color: red;
                margin-left: -2em;
            }

        }

        .btn-slice {
            padding: 1.2rem 2.5rem;
            border: 2px solid red;
            border-radius: 0rem;
            color: red;
            font-size: 0.6875rem;
            font-weight: 700;
            letter-spacing: 0.3rem;
            position: relative;
            cursor: pointer;

            -webkit-transition: all 1000ms cubic-bezier(0.890, 0, 0.140, 1);
            /* older webkit */
            -webkit-transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
            -moz-transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
            -o-transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
            transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
            /* custom */

            -webkit-transition-timing-function: cubic-bezier(0.890, 0, 0.140, 1);
            /* older webkit */
            -webkit-transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);
            -moz-transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);
            -o-transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);
            transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);

            /* custom */
            .top {
                position: relative;
                height: 10px;
                overflow: hidden;
                top: 0;
                -webkit-transition: all 300ms cubic-bezier(1.000, 0, 0.025, 1);
                /* older webkit */
                -webkit-transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -moz-transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -o-transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                /* custom */

                -webkit-transition-timing-function: cubic-bezier(1.000, 0, 0.025, 1);
                /* older webkit */
                -webkit-transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -moz-transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -o-transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                /* custom */

                -webkit-transition-delay: 0.35s;
                -moz-transition-delay: 0.35s;
                -ms-transition-delay: 0.35s;
                -o-transition-delay: 0.35s;
                transition-delay: 0.35s;
            }

            .bottom {
                position: relative;
                height: 10px;
                overflow: hidden;
                bottom: 0;
                -webkit-transition: all 300ms cubic-bezier(1.000, 0, 0.025, 1);
                /* older webkit */
                -webkit-transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -moz-transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -o-transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                transition: all 300ms cubic-bezier(1.000, -0.055, 0.025, 1.040);
                /* custom */

                -webkit-transition-timing-function: cubic-bezier(1.000, 0, 0.025, 1);
                /* older webkit */
                -webkit-transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -moz-transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                -o-transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                transition-timing-function: cubic-bezier(1.000, -0.055, 0.025, 1.040);
                /* custom */

                -webkit-transition-delay: 0.35s;
                -moz-transition-delay: 0.35s;
                -ms-transition-delay: 0.35s;
                -o-transition-delay: 0.35s;
                transition-delay: 0.35s;

                span {
                    top: -10px;
                    position: absolute;
                    left: 0;
                }
            }

            &:hover {
                .top {
                    top: -5px;
                    -webkit-transition-delay: 0.35s;
                    -moz-transition-delay: 0.35s;
                    -ms-transition-delay: 0.35s;
                    -o-transition-delay: 0.35s;
                    transition-delay: 0.35s;
                }

                .bottom {
                    bottom: -5px;
                    -webkit-transition-delay: 0.35s;
                    -moz-transition-delay: 0.35s;
                    -ms-transition-delay: 0.35s;
                    -o-transition-delay: 0.35s;
                    transition-delay: 0.35s;
                }
            }

            &:hover,
            &:focus,
            &:active {
                margin-left: 10px;
            }

            &::before {
                content: '';
                height: 1px;
                width: 60px;
                background-color: red;
                position: absolute;
                margin-top: 6px;
                right: -35px;

                -webkit-transition: all 1000ms cubic-bezier(0.890, 0, 0.140, 1);
                /* older webkit */
                -webkit-transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
                -moz-transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
                -o-transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
                transition: all 1000ms cubic-bezier(0.890, -0.170, 0.140, 1.225);
                /* custom */

                -webkit-transition-timing-function: cubic-bezier(0.890, 0, 0.140, 1);
                /* older webkit */
                -webkit-transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);
                -moz-transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);
                -o-transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);
                transition-timing-function: cubic-bezier(0.890, -0.170, 0.140, 1.225);
                /* custom */
            }

            &:hover:before {
                width: 130%;
            }
        }
    }


    .cours-home-separator {
        // margin-top: calc();
        // margin-top: -50%;
        transform: translateY(-80%);
        height: 80%;
        width: 1px;
        background-color: #fff;
        border-radius: 15px 15px;
        opacity: 1;
    }

    .right-container-home-cours {
        height: 100%;
        width: calc(50% - 1px);
        display: flex;
        flex-direction: column;
        position: absolute;
        right: -50%;
        transition: right 1s ease-in-out;
        transition-delay: 0.5s;

        .shpi-button-list-container {
            display: flex;
            justify-content: space-between;
            height: 7%;
            width: 100%;
            top: 20%;
            position: relative;
            z-index: 2;

            .home-shpi-container {
                height: 100%;
                width: 25%;

                .button-shpi-container {
                    height: 100%;
                    text-align: center;
                    width: 100%;
                    cursor: pointer;
                    color: white;
                    transition: all 0.3s;
                    position: relative;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    z-index: 1;
                    background-color: rgba(65, 65, 65, 0.5);
                    font-size: 0.6875rem;
                    font-weight: 700;

                    span {
                        transition: all 0.3s;
                    }

                    &::before {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        z-index: 1;
                        opacity: 0;
                        transition: all 0.3s;
                        border-top-width: 1px;
                        border-bottom-width: 1px;
                        border-top-style: solid;
                        border-bottom-style: solid;
                        border-top-color: rgba(228, 38, 24, 0.5);
                        border-bottom-color: rgba(228, 38, 24, 0.5);
                        transform: scale(0.1, 1);
                    }

                    &::after {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        z-index: -1;
                        transition: all 0.3s;
                    }

                    &.active::after {
                        opacity: 0;
                        transform: scale(0.1, 1);
                    }

                    &.active::before {
                        opacity: 1;
                        transform: scale(1, 1);
                    }

                    &.active {
                        color: red;
                        background-color: rgba(255, 255, 255, 0);

                        span {
                            letter-spacing: 2px;
                        }
                    }

                }

                &:nth-of-type(1) {
                    margin-left: 5%;
                }

                &:nth-of-type(3) {
                    margin-right: 5%;
                }

            }


        }

        .shpi-text-container {
            visibility: hidden;
            height: 7%;
            width: 25%;
            display: flex;
            flex-direction: column;
            position: absolute;
            z-index: 1;
            background-color: rgba(65, 65, 65, 0.5);
            top: 20%;
            margin-left: 5%;
            transition: height 1s, top 1s, position 1s, margin-left 1s 1s;

            .module {
                color: red;
                height: 10%;
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                font-size: 0.7rem;
                font-weight: 700;
                letter-spacing: 2px;
            }

            .matter-container {
                margin-top: 20%;
                height: 80%;
                width: 100%;
                display: flex;
                flex-direction: column;

                .matter {
                    color: white;
                    height: 15%;
                    width: 100%;
                    font-size: 0.6rem;
                    font-weight: 700;
                    letter-spacing: 2px;
                    word-break: break-all;
                    hyphens: auto;
                    cursor: pointer;

                    // &:hover {
                    //     text-decoration: underline;
                    //     text-underline-offset: 3px;
                    //     color: red;
                    // }
                }
